export class Logger {
    static debugSwitch = true;

    static debug(debugMessage: any): void;
    static debug(componentName: any, ...param: any[]): void;
    static debug(...param: any[]) {
        if (!Logger.debugSwitch) return;

        const baseMessage = ['%c', '%s', '%c'];
        const messageItem = [
            'background: #4081C6; color: #FFF; padding: 1px;',
            `${param.length > 1 ? param.shift() : 'DebugMessage'}`,
            'background:  #10A6AB; color: #FFF; padding: 1px;'
        ];

        param.forEach(item => {
            if (typeof item === 'object') {
                baseMessage.push('%O');
            } else {
                baseMessage.push('%s');
            }
            messageItem.push(item);
        });


        console.log(`${baseMessage.join(' ')} `, ...messageItem);
    }

    static log(...param: any[]) {
        console.log(...param);
    }

    static warn(...params: any[]) {
        console.warn(...params);
    }

    static error(...params: any[]) {
        console.error(...params);
    }
}
